body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  height: 100vh;
  overflow: hidden;
}

.home {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  position: relative;
  max-width: 100%;
  height: 100%;
}

.home-icons {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 2em;
  width: -webkit-max-content;
  width: max-content;
  z-index: 10;
}

.home-icons a {
  display: flex;
  align-items: center;
  padding: 10px;
  transition: padding 0.3s ease;
  color: #000;
  text-decoration: none;
  width: -webkit-max-content;
  width: max-content;
  overflow: hidden;
}

.home-icons a span {
  margin-left: 10px;
  visibility: hidden;
  opacity: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: 
    visibility 0s 0.3s, 
    opacity 0.3s ease, 
    -webkit-transform 0.3s ease;
  transition: 
    visibility 0s 0.3s, 
    opacity 0.3s ease, 
    transform 0.3s ease;
  transition: 
    visibility 0s 0.3s, 
    opacity 0.3s ease, 
    transform 0.3s ease, 
    -webkit-transform 0.3s ease;
  white-space: nowrap;
}

.home-icons a:hover span {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition-delay: 0s, 0s, 0s;
}


@media (max-width: 768px) {
  .home-icons {
    display: none;
  }
}

.plexus-background {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.home-content {
  z-index: 10;
  font-family: 'Raleway', sans-serif; 
  color: #000;  
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100% - 60px);
}

.home-content h1 {
  font-size: clamp(2em, 5vw, 7em);
  font-weight: 700;
}

.home-content h2 {
  font-size: clamp(2em, 3.5vw, 3.3em);
  font-weight: 600;
}

.home-content h3 {
  font-size: clamp(1.75em, 3vw, 3em);
  font-weight: 600;
}

.home-content h4 {
  font-size: clamp(1.5em, 2.5vw, 2em);
  font-weight: 600; 
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.7); /* Translucent black */
  z-index: 20;
  padding: 5px;
  transition: background-color 0.3s ease; /* Transition for smooth change */
}

.navbar:hover {
  background-color: #000; /* Solid black on hover */
}

.navbar-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 20;
}

.navbar-toggle:focus {
  outline: none;
}

.navbar-toggle .bar {
  width: 2rem;
  height: 0.25rem;
  background-color: #fff;
  border-radius: 10px;
  transition: all 0.3s linear;
  position: relative;
  -webkit-transform-origin: 1px;
          transform-origin: 1px;
}

.navbar ul {
  display: flex;
  justify-content: center;
  width: 100%;
  list-style: none;
}

.navbar li {
  padding: 0 1rem;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  letter-spacing: 2px;
  font-size: clamp(0.8rem, 3vw, 1rem);
}

.navbar a:hover,
.navbar a:focus {
  color: #f8f1f1;
  outline: none;
}

.navbar-icons {
  display: flex;
  justify-content: flex-end;
}

.navbar-icon {
  margin: 0 0.5rem;
}

@media (max-width: 768px) {
  .navbar-toggle {
    display: flex;
  }

  .navbar ul {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 60px;
    left: 0;
    background-color: #000;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }

  .navbar ul.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  .navbar li {
    padding: 2rem;
  }

  .navbar a {
    font-size: clamp(1.5rem, 4vw, 2rem);
  }

  .navbar-icons {
    flex-direction: row;
    justify-content: center;
  }
}

.no-scroll {
  overflow: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, #root, .App {
  height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
}
body {
  overflow-y: auto;
  margin: 0;
  padding: 0;
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 100%;
}

.plexus-background {
  position: absolute;
  width: 100%;
  height: 100%;
}

.about-container {
  z-index: 10;
  position: relative;
  max-width: 80%;
  margin: auto;
  height: 100%;
  padding: 2rem 0;
  padding-top: 60px;
}

.about-content-effect {
  height: 4.5em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-content h1 {
  font-size: clamp(2.5em, 5vw, 5em);
  font-weight: 700;
}

.about-content-effect h2 {
  font-size: clamp(2em, 3.5vw, 3.3em);
  font-weight: 600;
  color: #616161;
  margin-bottom: 20px;
}

.personal-section {
  z-index: 10;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  margin: auto;
  max-width: 80%;
}

.personal-section .personal-image, .personal-section .personal-info {
  flex: 1 1;
  padding: 1rem;
}

.personal-image img {
  width: 100%;
  height: auto;
  border-radius: 50%;
}

.personal-section .personal-info {
  font-size: clamp(1em, 2vw, 1.5em);
  font-weight: 500;
  line-height: 1.5;
}

.github-link {
  color: rgb(0, 0, 0);
  text-decoration: underline;
  font-weight: bold;
}

@media screen and (max-width: 768px) {

  .personal-section {
    flex-direction: column;
    padding-bottom: 100px;
  }

  .about-container {
    z-index: 10;
    position: relative;
    max-width: 80%;
    margin: auto;
    height: 100%;
    padding: 2rem 0;
    padding-top: 400px;
  }
}

.resume-icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    text-decoration: none;
    color: inherit;
  }
  
  .resume-icon span {
    margin-top: 10px;
  }
  
.resume {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 60px;
  }
  
  .plexus-background {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  
  .resume-container {
    z-index: 10;
    position: relative;
    max-width: 80%;
    margin: auto;
    height: 100%;
  }

  .resume-content {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .resume-content h1 {
    font-size: clamp(2.5em, 5vw, 5em);
    font-weight: 700;
  }
  
  .resume-icons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px 0;
    background-color: rgba(200, 200, 200, 0.5);
    border-radius: 50px;
  }
  
  .resume-icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    text-decoration: none;
    color: inherit;
  }
  
  .resume-icon span {
    margin-top: 10px;
  }
  
  .resume-pdf {
    display: flex;
    justify-content: center;
    padding: 20px 0;
  }
  
  .resume-pdf iframe {
    width: 80%;
    height: 70vh;
  }
  
  @media (max-width: 768px) {
    .resume-pdf iframe {
      width: 100%;
    }
  }
  
.contact {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    width: 100%;
    height: calc(100% - 60px);
}

.plexus-background {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.contact-title {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
    padding-top: 60px;
}

.contact-title h1 {
    font-size: clamp(4em, 5vw, 5em);
    font-weight: 700;
}

.contact-container {
    z-index: 10;
    position: relative;
    width: 45%;
    margin: auto;
    height: 100%;
    padding: 2rem 0;
    padding-top: 60px;
}

.contact-container form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 10px;
    padding: 40px; 
}

.contact-container form input, .contact-container form textarea {
    width: 100%;
    padding: 1.5em;
    margin-bottom: 1em;
    border-radius: 5px;
    border: 2px solid #ccc;
    font-size: 1.2em;
    background-color: #fff;
}

.contact-container form button {
    padding: 15px 30px;
    border: none;
    border-radius: 5px;
    background-color: #000;
    color: #fff;
    cursor: pointer;
    font-size: 1.4em;
}

@media screen and (max-width: 768px) {
    .contact-container {
        width: 95%;
    }

    .contact-container form {
        padding: 20px;
    }
}

